* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

:root {
  --cal-tile-height: 3.5em;
  --cal-tile-gap: 3px;
  --cal-tile-fontsize: 1.4em;
}

.cal-grid-container {
  display:  grid;
  margin: 0 1em;
  grid-template-columns: repeat(7, 1fr);
  grid-template-rows: auto;
  gap: var(--cal-tile-gap);
}

.cal-grid-container > div {
  text-align: center;
}

.dashboard {
  width: 80%;
  max-width: 400px;
  height: 100vh;
  position: absolute;
  top: 0;
  bottom: 0;
  background-color: rgb(231, 226, 226);
  z-index: 2;
  padding: 1.3em;
  transition: transform 500ms;
  overflow: auto;
}

.btn-dashboard {
  display: inline;
  float: right;
  width: 50%;
  border: 1px solid transparent;
  border-radius: 5px;
  padding: 0.3em 0;
  background-color: rgb(240, 238, 238);
}

.horizontal-break {
  clear: both;
  display: block;
  width: 100%;
  height: 2px;
  background: rgb(240, 238, 238);
}

.calender-title {
  text-align: center;
  font-size: 1.8em;
  font-family: monospace;
}

.sm-tooltip {
  font-size: 0.7em;
  color: white;
  background-color: rgb(70, 61, 61);
  position: absolute;
  max-width: 50%;
  left: 50%;
  transform: translateX(-50%);
  padding: 0.1em 1em;
  border-radius: 10px;
  z-index: 10;
}

.drop-down-menu {
  display: flex;
  flex-direction: column;
  width: 20%;
  min-width: 100px;
  position: absolute;
  right: 0;
  padding: 1em;
  justify-content: center;
  align-items: center;
  background-color: rgb(122, 122, 122);
  z-index: 10;
  margin: 0.5em;
  border-radius: 10px;
}

.drop-down-menu button {
  width: 100%;
  margin: 0.4em auto;
  padding: 0.3em 0.2em;
  border-radius: 10px;
  background-color: rgb(236, 235, 235);
  border: 1px solid gray;
  color: rgb(95, 95, 94);
  font-weight: 700;
  font-family: monospace;
  transition: color 0.3s, background-color 0.3s;
}

.drop-down-menu button:hover {
  background-color: rgb(75, 72, 72);
  color: white;
}

.translucent-background {
  z-index: 20;
  background-color: rgba(128, 128, 128, 0.397);
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.transparent-background {
  z-index: 20;
  position: absolute;
  width: 100%;
  height: calc(100vh - 5em);
  background-color: red;
}

.confirm-delete-box {
  padding: 1em;
  background-color: white;
}

.confirm-delete-box .buttons {
  display: flex;
  width: 90%;
  margin: 10px auto;
  justify-content: space-between;
  align-items: center;
}

.main-navbar {
  position: sticky;
  display: flex;
  justify-content: space-between;
  padding: 0.7em 2em;
  background-color: rgb(248, 248, 248);
}

.secondary-navbar {
  display: flex;
  justify-content: space-between;
  width: 90%;
  margin: 1em auto;
}

.button-md {
  font-size: 1.3em;
  border: 1px solid transparent;
  background-color: transparent;
}

.calendar-tile {
  height: var(--cal-tile-height);
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: var(--cal-tile-fontsize);
}

.btn-gn {
  padding: 0.2em;
  margin: 0.1em 0;
  border: 1px solid transparent;
  background-color: transparent;
}
.dashboard-close-btn {
  font-size: 1.7em;
  float: right;
  transform: rotate(0deg);
  transition: transform 200ms;
}

.dashboard-close-btn:hover {
  transform: rotate(45deg);
}

.create-new-btn {
  transition: background-color 0.5s, color 0.5s;
}

.create-new-btn:hover {
  background-color: rgb(36, 35, 35);
  color: white;
}

.dashboard-title {
  clear: both;
  font-size: 1.5em;
  font-family: monospace;
}

.task-list-item {
  list-style: none;
  cursor: pointer;
  background-color: rgb(255, 255, 255);
  margin: 1em auto;
  padding: 0.5em 1em;
  border-radius: 15px;
  border: 2px solid rgb(189, 185, 185);
}

.task-selected {
  border: 1px solid white !important;
  outline: 4px solid rgb(7, 39, 6) !important;
}

.calendar-date-marked {
  background-color: rgb(114, 212, 16);
}

.task-marked {
  border: 1px solid transparent;
  background-color: rgb(70, 124, 15);
  color: white;
}

.add-new-form {
  margin: 0 auto;
  background-color: white;
  padding: 1em 1.5em;
  border-radius: 5px;
}

.add-new-form > div {
  width: 80%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}

.form {
  margin: 0 auto;
  background-color: white;
  padding: 1em 1.5em;
  border-radius: 5px;
  width: 80%;
  max-width: 500px;
}

.form-sm {
  max-width: 350px;
}

.form-input {
  width: 100%;
  padding: 0.2em 0.7em;
  border-radius: 5px;
  border: 2px solid gray;
  margin: 0.1em 0 0.7em 0;
}

.form-label-text {
  color: gray;
  font-size: 1.2em;
}

.form-title {
  text-align: center;
  font-size: 1.2em;
  color: rgb(0, 0, 43);
  margin: 0;
}

.error-msg {
  clear: both;
  text-align: center;
  color: red;
  margin: 0;
}

.buttons {
  display: flex;
  justify-content: space-between;
  width: 80%;
  margin: 0 auto;
}

.buttons button {
  font-size: 1.2em;
}

.m-b {
  margin-bottom: 1em;
}

.rotate-btn {
  transition: transform 0.5s;
  padding: 0.1em;
}

.rotate-btn:hover {
  transform: rotate(90deg);
}

.calendar-today {
  border: 0.25em solid rgb(7, 51, 11);
}

@media screen and (max-width: 400px) {
  .calendar-tile {
    --cal-tile-height: 2.5em;
    --cal-tile-fontsize: 1.3em;
  }
}